import React from 'react';
import Header from './Header';
import Box from './Box';
import Form from './Form';
import { toHaveFormValues } from '@testing-library/jest-dom/dist/matchers';

const App = () => {
  const [darkMode, setDarkMode] = React.useState(false);
  const [dataRegion, setDataRegion] = React.useState([]);

  const [searchData, setSearchData] = React.useState('');
  const [region, setRegion] = React.useState('');

  {
    document.body.style.backgroundColor = darkMode ? '#202d36' : '#fafafa';
  }
  /* dark mode function */

  const toggleDark = () => {
    setDarkMode(!darkMode);
  };
  React.useEffect(() => {
    fetch('https://restcountries.com/v2/all')
      .then((res) => res.json())
      .then((data) => setDataRegion(data));
  }, []);
  const handleSearch = (event) => {
    const value = event.target.value;
    setSearchData(value);
  };
  const handleRegion = (value) => {
    setRegion(value);
  };
  const dataEl = dataRegion.map((item, index) => {
    return (
      <Box
        key={index}
        flag={item.flag}
        name={item.name}
        population={item.population}
        region={item.region}
        capital={item.capital}
        regionState={region}
        searchData={searchData}
      />
    );
  });
  return (
    <main className={`${darkMode && 'dark'}`}>
      <Header dark={toggleDark} darkMode={darkMode} />
      <Form
        searchData={searchData}
        handleSearch={handleSearch}
        region={region}
        handleRegion={handleRegion}
        darkMode={darkMode}
      />
      <div className='country-parent'>
        <div className='container'>{dataEl}</div>
      </div>
    </main>
  );
};

export default App;
