import React from 'react';

const Box = (props) => {
  if (
    !props.searchData ||
    props.name.toLowerCase().includes(props.searchData)
  ) {
    if (
      props.regionState === 'all' ||
      !props.regionState ||
      props.regionState.toLowerCase() === props.region.toLowerCase()
    ) {
      return (
        <div className='box'>
          <img src={props.flag} />
          <div className='text'>
            <h4>{props.name}</h4>
            <div className='info'>
              <p>
                Population:<p> {props.population}</p>
              </p>
              <p>
                Region:<p> {props.region}</p>
              </p>
              <p>
                capital:<p> {props.capital}</p>
              </p>
            </div>
          </div>
        </div>
      );
    }
  }
};

export default Box;
