import React from 'react';
import { AiOutlineSearch } from 'react-icons/ai';
import Select from 'react-select';
const options = [
  { value: 'all', label: 'All' },
  { value: 'africa', label: 'Africa' },
  { value: 'Americas', label: 'Americas' },
  { value: 'asia', label: 'Asia' },
  { value: 'europe', label: 'Europe' },
  { value: 'oceania', label: 'Oceania' },
];

const Form = (props) => {
  const customStyles = {
    input: (provided, state) => ({
      ...provided,
      color: props.darkMode ? 'white' : 'black',
    }),
    placeholder: (provided, state) => ({
      ...provided,
      color: props.darkMode ? '#E6E6FA' : '#777',
    }),
    container: (provided, state) => ({
      ...provided,
      minWidth: '180px',
    }),
    control: (provided, state) => ({
      ...provided,
      backgroundColor: props.darkMode ? '#2b3743' : 'white',
      border: 'none',
      boxShadow: props.darkMode
        ? '0px 0px 9px 0px #0a06289c'
        : '0px 0px 9px 0px #dddddd9c',
    }),
    singleValue: (provided, state) => ({
      ...provided,
      color: props.darkMode ? 'white' : 'black',
    }),
    menu: (provided, state) => ({
      ...provided,
      backgroundColor: props.darkMode ? '#2b3743' : 'white',
    }),
    option: (provided, state) => ({
      ...provided,
      color:
        props.darkMode || state.isSelected
          ? 'white'
          : state.isFocused && props.darkMode
          ? 'black'
          : 'black',
    }),
  };
  return (
    <form onSubmit={(e) => e.preventDefault()}>
      <div className='container'>
        <div className='search-bar'>
          <AiOutlineSearch />
          <input
            type='text'
            placeholder='Search for a country'
            name='search'
            value={props.searchData}
            onChange={props.handleSearch}
          />
        </div>
        <Select
          options={options}
          onChange={(e) => props.handleRegion(e.value)}
          styles={customStyles}
        />
      </div>
    </form>
  );
};

export default Form;
